
.confirmation__entering {
  position: absolute;
  top: 150%;
  left: 10%;
  padding-right: 10%;
  /*transform: translate(0, -50%);*/
}


.confirmation {
  transition: 750ms;
  position: absolute;
  top: 30%;
  left: 10%;
  padding-right: 10%;
  /*border: 1px solid red;*/
}

.confirmation__exiting {
  transition: 750ms;
  position: absolute;
  top: -50%;
  left: 10%;
  padding-right: 10%;
  /*transform: translate(0, -50%);*/
}

.confirmation__question {
  color: white;
  margin-bottom: 2rem;
}

.confirmation__button-wrapper {
  display: flex;
  flex-direction: column;
}

.confirmation__button {
  background-color: rgba(200, 200, 200, 0.3);
  border: 1px solid #fff;
  color: white;
  padding: 0.5rem 1.5rem;
  width: 10rem;
  border-radius: 5px;
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  outline: none;
}

.confirmation-button-clicked {
  background-color: rgba(225, 225, 225, 0.6);
  color: rgb(225,225,225);
  border: 1px solid rgb(225,225,225);
  outline: #fff;
}